import React from 'react';
import { Link } from 'react-router-dom';


const NavBar = () => (
    
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
  <div className="container ">
      <Link className="navbar-brand justify-content-center" to="/">MyTechBlog</Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation"><span className="navbar-toggler-icon"></span></button>
      <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item"><Link className="nav-link" to="/">Home</Link></li>
              <li className="nav-item"><Link className="nav-link" to="/about">About</Link></li>
              <li className="nav-item"><Link className="nav-link" to="/articles">Articles</Link></li>
              <li className="nav-item"><Link className="nav-link active" to="https://github.com/melvin-zike?tab=repositories">Github Repo</Link></li>
          </ul>
      </div>
  </div>
</nav>

    
    )

    export default NavBar;