import React from 'react';
import { FaThumbsUp } from 'react-icons/fa'

const Likes = ({ articleName, likes, setArticleInfo}) => {
 const likeArticle = async () => {
    const result = await fetch(`/api/articles/${articleName}/likes`, {
        method: 'post',
     });
     const body = await result.json();
     setArticleInfo(body);
 }


return (
<div className="mt-2">
    
    <span className="spanlike"> <FaThumbsUp onClick={() => likeArticle()} /></span>
    <span className="p-1">{likes}</span>
    
</div>
)
   
    
}

export default Likes;